/* Header */

.header {
  padding: 40px 0;
  min-height: 695px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: var(--header-bg);
  background-image: url('./../../img/header-bg.png');
  background-repeat: no-repeat;
  background-size: auto;
  background-position: center center;

  color: var(--header-text);
  text-align: center;

  @media (max-width: 640px) {
    min-height: 100vh;
    padding: 20px 0;
  }



  &__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;

    @media (max-width: 640px) {
      flex-direction: column;
    }
  }

  &__wrapper {
    padding: 0 15px;
    max-width: 660px;




  }

  &__title {
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: 700;
    line-height: 1.4;

    @media (max-width: 640px) {
      font-size: 28px;
    }
  }

  &__title strong {
    font-size: 60px;
    font-weight: 700;

    @media (max-width: 640px) {
      font-size: 48px;
    }
  }

  &__title em {
    font-style: normal;
    color: var(--accent);
  }

  &__text {
    margin-bottom: 40px;
    font-size: 18px;
    line-height: 1.333;

    @media (max-width: 640px) {
      font-size: 16px;
    }
  }

  &__text p+p {
    margin-top: 0.5em;
  }
}

/* Btn */

.btn {
  display: inline-block;
  height: 48px;
  padding: 12px 28px;

  border-radius: 5px;
  background-color: var(--accent);
  color: var(--white);
  letter-spacing: 0.15px;
  font-size: 16px;
  font-weight: 500;

  transition: opacity 0.2s ease-in;

  &:nth-last-child(2n) {
    background-color: #e0dfdf;
    color: #000000;
  }

}

.btn:hover {
  opacity: 0.8;
}

.btn:active {
  position: relative;
  top: 1px;
}

/* Btn outline */

.btn-outline {
  display: flex;
  column-gap: 10px;
  align-items: center;

  height: 48px;
  padding: 12px 20px;

  border-radius: 5px;
  border: 1px solid #000;
  background-color: #fff;
  color: #000;

  transition: opacity 0.2s ease-in;
}

.btn-outline:hover {
  opacity: 0.8;
}

.btn-outline:active {
  position: relative;
  top: 1px;
}
